<template>
  <el-container>
    <el-header height="100px" class="neumorphism my-title"><h1>辛未年时的照片展示</h1></el-header>
    <el-main>
      <div class="grid" id="images">
<div class="grid-item" v-for="(image,index) in imageInfo" :key="index">
  <el-card :body-style="{padding:0}" shadow="hover">
    <img :data-original="image.original"
         :src="image.thumbnail" style="margin-left: 5px;cursor: zoom-in" alt="">
  </el-card>
</div>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import Masonry from 'masonry-layout';
import imagesloaded from 'imagesloaded';
import anime from 'animejs';
import Viewer from 'viewerjs';
import 'viewerjs/dist/viewer.css';
import { getImageInfo } from '@/service/api';

export default {
  name: 'Images',
  data() {
    return {
      imageInfo: [],
      imgArr: [
        'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3472263623,506218584&fm=26&gp=0.jpg',
        'http://www.sinaimg.cn/dy/slidenews/21_img/2015_17/2236_4146071_705561.jpg',
        'http://www.sinaimg.cn/dy/slidenews/21_img/2015_17/2236_4146072_346494.jpg',
      ],
    };
  },
  updated() {
    const grid = document.querySelector('.grid');
    const msnry = new Masonry(grid, {
      // options...
      itemSelector: '.grid-item',
      columnWidth: 50,
    });
    imagesloaded('.grid-item', () => {
      msnry.layout(); // 图片加载完成后重新绘制。
    });
    const viewer = new Viewer(document.getElementById('images'), {
      url: 'data-original',
    });
  },
  beforeCreate() {
    document.title = '沧沧凉凉的照片';
  },
  created() {
    getImageInfo()
      .then((res) => {
        this.imageInfo = res.data;
      });
  },
  mounted() {
    anime({
      targets: '.my-title h1',
      translateX: 300,
      direction: 'alternate',
      easing: 'spring(1, 80, 10, 0)',
      delay: 500,
    });
  },
};
</script>

<style scoped lang="scss">

  .grid {
    padding-left: 35px;
    width: 100%;
    height: 690px !important;
    overflow: auto;
  }
</style>
