<template>
  <el-container>
    <el-header height="100px" class="neumorphism my-title">
      <h1>我的文章</h1>
    </el-header>
    <el-main style="margin: 30px">

      <div
        v-for="(article,index) in articles" :key="index">
        <a class="article" target="view_window"
           :href="`http://www.erbao.cf/mylog/index#/blog/articles/${article.id}`">{{`·
          ${article.title}`}}
        </a>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import anime from 'animejs';

export default {
  name: 'Article',
  computed: {
    articles() {
      return this.$store.state.articles;
    },
  },
  beforeCreate() {
    document.title = '沧沧凉凉的文章';
  },
  created() {
    this.$store.dispatch('getArticles');
  },
  updated() {
    anime({
      targets: '.article',
      opacity: 1,
      delay: anime.stagger(100), // 每个元素的延迟增加100毫秒。
    });
  },
};
</script>

<style lang="scss" scoped>
  .article {
    display: block;
    margin: 20px 0;
    font-size: 20px;
    cursor: pointer;
    opacity: 0;

    &:hover {
      color: #0086b3;
    }
  }
</style>
