<template>
  <div class="musicPlayer neumorphism">
    <el-row style="height: 100%;">
      <el-col class="image" :span="3">
        <el-avatar
          :src="musicList[musicIndex].musicImg">
        </el-avatar>
        <p>{{musicList[musicIndex].musicName}}</p>
      </el-col>
      <el-col
        style="height: 100%;display: flex;flex-direction: column;align-items: center;
        justify-content: space-around"
        :span="21">
        <div class="progressBar">
          <div class="before" @click="turnMusic"></div>
          <div ref="after" class="after" @click="turnMusic" @touchmove="turnMusic"></div>
          <div ref="progressBarButton" class="progressBarButton"></div>
        </div>
        <ul class="button">
          <li @click="()=>{
            this.$store.commit('beforeMusic');
            this.reset();
          }">
            <span class="iconfont icon-1_music83"/></li>
          <li v-if="!musicStatus" class="el-icon-video-play"
              @click="()=>{
                this.$store.commit('playMusic');
              }"
          ></li>
          <li v-if="musicStatus" class="el-icon-video-pause"
              @click="()=>{
                this.$store.commit('playMusic');
              }"
          ></li>
          <li @click="()=>{
            this.$store.commit('lastMusic');
            this.reset();
          }">
            <span class="iconfont icon-1_music82"/></li>
        </ul>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'MusicPlayer',
  data() {
    return {
      timer: Function,
    };
  },
  computed: {
    musicList() {
      return this.$store.state.musicList;
    },
    musicStatus() {
      return this.$store.state.musicStatus;
    },
    musicIndex() {
      return this.$store.state.musicIndex;
    },
    music() {
      return this.$store.state.music;
    },
  },
  methods: {
    turnMusic(event) {
      this.$refs.progressBarButton.style.left = `${event.offsetX - 10}px`;
      this.$refs.after.style.width = `${event.offsetX}px`;
      this.$store.commit('tureMusic', event.offsetX / 725);
    },
    reset() {
      this.$refs.progressBarButton.style.left = '0px';
      this.$refs.after.style.width = '0px';
    },
  },
  mounted() {
    this.timer = setInterval(() => {
      const percentage = this.$store.state.music.currentTime / this.$store.state.music.duration;
      this.$refs.progressBarButton.style.left = `${725 * percentage - 10}px`;
      this.$refs.after.style.width = `${725 * percentage}px`;
    }, 1000);
  },
  destroyed() {
    clearInterval(this.timer);
  },
};
</script>

<style lang="scss" scoped>
  @import "src/styles/main";

  .musicPlayer {
    width: 100%;
    height: 105px;
    border-radius: 10px;

    .image {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100%;
    }

    .progressBar {
      width: 80%;
      position: relative;

      .before,
      .after {
        top: 10px;
        height: 10px;
        position: absolute;
        cursor: pointer;
      }

      .before {
        width: 725px;
        background-color: #ecedf1;
      }

      .after {
        width: 0;
        background-color: #41c788;
      }

      .progressBarButton {
        position: absolute;
        height: 20px;
        width: 20px;
        top: 10px;
        border-radius: 50%;
        transform: translateY(-25%);
        background-color: #41c788;
      }
    }

    .button {
      display: flex;
      font-size: 30px;
      align-items: center;
      justify-content: center;

      li {
        margin: 0 30px;
        cursor: pointer;
      }

      span {
        font-size: 30px;
      }
    }
  }
</style>
