<template>
  <div class="home">
    <!--主界面-->
    <el-container class="plan">
      <!--导航栏-->
      <el-aside class="button" width="115px">
        <span @click="clickButton(0)" :class="`${count===0?'click':''} iconfont icon-shouye`"/>
        <span @click="clickButton(1)" :class="`${count===1?'click':''} iconfont icon-chuangzuo`"/>
        <span @click="clickButton(2)" :class="`${count===2?'click':''} iconfont icon-taolunqu`"/>
        <span @click="clickButton(3)" :class="`${count===3?'click':''} iconfont icon-tupian`"/>
        <span @click="clickButton(4)" :class="`${count===4?'click':''} el-icon-menu`"/>
      </el-aside>
      <div class="showPlan">
        <!--主页-->
        <About v-if="count===0"/>
        <!--留言页-->
        <Comment v-if="count===1"/>
        <!--文章页-->
        <Article v-if="count===2"/>
        <!--照片页-->
        <Images v-if="count===3"/>
        <!--更多的页面-->
        <Other v-if="count===4"/>
      </div>
    </el-container>
    <audio ref="audio" :src="musicList[musicIndex].musicUrl"/>
  </div>
</template>

<script>

import Comment from '@/views/Home/Comments.vue';
import Article from '@/views/Home/Articles.vue';
import Images from '@/views/Home/Images.vue';
import About from '@/views/Home/About.vue';
import Other from '@/views/Home/Other.vue';

export default {
  name: 'Home',
  components: {
    Other,
    About,
    Images,
    Article,
    Comment,
  },
  data() {
    return {
      count: 0,
    };
  },
  computed: {
    musicIndex() {
      return this.$store.state.musicIndex;
    },
    musicList() {
      return this.$store.state.musicList;
    },
  },
  methods: {
    clickButton(index) {
      this.count = index;
    },
  },
  mounted() {
    this.$store.dispatch('getMusicList');
    this.$store.commit('setMusic', this.$refs.audio);
  },
};
</script>

